@import url("https://fonts.googleapis.com/css?family=Montserrat:400,700");
@import url("https://fonts.googleapis.com/css2?family=Nunito&family=Roboto&family=Ubuntu&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@300;400&display=swap");

.services-page {
  font-family: "Montserrat", sans-serif;
}

.services-page-title-div {
  padding-bottom: 5rem;
}

.services-page-content-div {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.services-page-title {
  max-width: 60rem;
  color: rgba(0, 0, 0, 0.884) !important;
  text-align: center;
  font-family: "Montserrat", sans-serif;
  font-size: 2.2rem;
  margin-top: 3rem;
  border-top: 2px solid rgb(95, 68, 41);
  border-bottom: 2px solid rgb(56, 56, 56);
  border-radius: 999999px;
  padding: 2rem 5rem;
  letter-spacing: 0.3rem;
  font-weight: 600;
}

.services-index-cols {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-align: center;
}

.services-card2 {
  color: rgb(61, 68, 83);
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-align: center;
  border-radius: 100%;
  margin-top: 4rem;
}

.cardTitle2 {
  font-size: 1rem;
  padding: 1rem;
  cursor: pointer;
}

.cardTitle2:hover {
  color: rgba(210, 171, 44, 0.91);
}

.index-icon {
  color: rgba(210, 171, 44, 0.91);
  font-size: 5rem !important;
}

.coffee-icon {
  font-size: 4.5rem !important;
}

.index-link {
  text-decoration: none !important;
  color: rgba(51, 57, 85, 0.89) !important;
}

@media screen and (max-width: 1300px) {
  .index-element-mask1,
  .index-element-mask2,
  .index-element-mask3,
  .index-element-mask4 {
    display: none;
  }
}

@media screen and (max-width: 1200px) {
  .services-page-title-div {
    padding: 0;
  }

  .secondRow3 {
    padding: 0rem 1.5rem;
    padding-top: 2rem;
  }

  .services-card2 {
    padding: 0 1rem;
    margin-top: 2rem;
    margin-bottom: 2rem;
  }

  .services-page-title {
    max-width: 60rem;
    font-size: 1.5rem;
    margin-top: 1rem;
    margin-bottom: 0rem;
    padding: 1rem 2rem;
    letter-spacing: 0.2rem;
  }

  .cardTitle2 {
    font-size: 0.6rem;
    padding: 0rem;
  }

  .learn-more-a-tag {
    font-size: 0.5rem;
  }

  .index-icon {
    color: rgba(255, 255, 255, 0.89);
    font-size: 2rem;
  }

  .coffee-icon {
    font-size: 3.5rem !important;
  }
}

.services-fp-mainRow {
  background: url(https://i.ibb.co/hHJMDwv/services-fp-background.jpg) no-repeat
    center;
  background-size: contain;
  background-attachment: fixed;
  background-position: left;
  width: 100%;
  min-height: 41rem;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.services-fp-firstPart {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  color: white;
  background-color: #333e51e7;
  border-radius: 100%;
  width: 100%;
  padding: 5.5rem 0rem;
}

.services-fp-firstPart-content-div {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  text-align: left;
  flex-direction: column;
  padding-left: 2rem;
}

.services-fp-h1-title {
  font-weight: 600;
  font-family: "Montserrat", sans-serif;
  letter-spacing: 0.3rem;
  font-size: 3.5rem;
  padding: 1rem 0rem;
  text-align: left;
}

.service-fp-para-text {
  padding: 1rem 0rem;
}

.services-fp-button {
  margin: 2rem 0 !important;
  color: rgb(255, 255, 255) !important;
  border-radius: 999999px !important;
  background-color: #333e51e7 !important;
  padding: 0.4rem 2.3rem !important;
}
.services-page-image-icon {
  margin-bottom: 1rem;
}

@media screen and (max-width: 1200px) {
  .services-fp-mainRow {
    background: url(https://i.ibb.co/hHJMDwv/services-fp-background.jpg)
      no-repeat center;
    background-size: cover;
    background-attachment: fixed;
    background-position: center;
    width: 100%;
    min-height: 100%;
  }
  .services-page-content-div {
    padding-top: 3rem;
  }

  .services-fp-firstPart {
    border-radius: 0;
    width: 100%;
    padding: 0rem;
    background-color: #333e51c2;
  }

  .services-fp-h1-title {
    font-weight: 600;
    font-family: "Montserrat", sans-serif;
    font-size: 2.1rem;
    text-align: left;
  }

  .index-icon {
    color: rgba(210, 171, 44, 0.91);
    font-size: 2.5rem;
    margin-bottom: 1rem;
  }

  .secondRow3 {
    padding: 0;
  }
}
