@import url("https://fonts.googleapis.com/css2?family=Ubuntu&display=swap");
@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400&family=Playfair+Display&family=Raleway&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Domine:wght@600&display=swap');


.landing-page {
    height: 100vh;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.overlay {
    display: flex;
    justify-content: center;
}

.overlay::after {
    width: 100%;
    height: 100%;
    content: '';
    position: absolute;
    z-index: 10;
    background: rgba(0, 0, 0, 0.5)
}

.home-video-background {
    width: 100%;
    height: 100%;
    position: relative;
    top: 0;
    left: 0;
    z-index: 5;
    object-fit: cover;
    font-family: 'object-fit: cover';
}

.home {
    position: absolute;
    z-index: 20;
    text-align: center;
    color: #ffffff;
}

.home-title-row-container {
    margin-left: 2rem;
    padding-left: 1.5rem;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
}

.home-title-row-container>.row1-cols {
    margin-bottom: 1.3rem;
}

.home-section {
    min-height: 47vw;
    min-width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.home-title {
    font-weight: 600;
    letter-spacing: 0.1rem;
    font-family: "Montserrat", sans-serif;
    color: rgba(255, 255, 255, 0.918);
    font-size: 3.1rem;
    padding: 0rem;
}

.home-subtitle-col {
    display: flex;
    align-items: center;
    justify-content: center;
}

.home-subtitle {
    line-height: 1.3rem;
    max-width: 50rem;
    font-family: "Montserrat", sans-serif;
    color: rgb(216, 216, 216);
    font-size: 0.9rem;
    padding: 0;
}

.home-subtitle-span {
    color: #D2AC2C;
}

.aboutLink {
    text-decoration: none !important;
    color: white !important;
}

.home-button-contact-us {
    color: rgb(255, 255, 255) !important;
    border-radius: 999999px !important;
    background-color: #333e51 !important;
    padding: 0.4rem 2.3rem !important;
}

/*-------------------------------------- HOME SECTION ending------------------------------------------------*/
/*-------------------------------------- MEDIA QUERIES------------------------------------------------*/

@media screen and (max-width: 1200px) {
    .home-title-row-container {
        margin-left: 0rem;
        padding-left: 0rem;
    }

    .home-title {
        font-size: 2.1rem;
        padding-top: 2rem;
    }

    .home-subtitle {
        line-height: 1.2rem;
        max-width: 20rem;
        font-size: 0.8rem;
        padding: 0;
    }
}

@media screen and (min-width: 1000px) {}