@import url("https://fonts.googleapis.com/css?family=Montserrat:400,700");
@import url("https://fonts.googleapis.com/css2?family=Nunito&family=Roboto&family=Ubuntu&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@300;400&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Domine:wght@600&display=swap");

.contact {
  font-family: "Montserrat", sans-serif;
  background: white;
  color: rgb(255, 255, 255);
}

.googleMap-div {
  width: 100%;
  height: 100%;
  position: relative;
  top: 0;
  left: 0;
  z-index: 0;
  object-fit: cover;
  font-family: "Montserrat", sans-serif;
  height: 100vh;
  width: 100%;
}

.contact-us-floating-application-form {
  top: 18%;
  left: 5%;
  border-radius: 10%;
  padding: 2rem;
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background-color: rgba(51, 62, 81, 0.748);
  z-index: 10;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}

.conact-us-in-person {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0rem;
  text-align: center;
}

.conact-us-in-person-h6 {
  font-size: 0.8rem;
}

.conact-us-in-person-span {
  font-size: 0.7rem;
  color: rgb(211, 176, 63);
}

.secondRow2-title {
  text-align: center;
  font-size: 1.5rem;
  color: rgb(255, 255, 255);
  font-family: "Montserrat", sans-serif;
  margin-bottom: 2rem;
  letter-spacing: 0.1rem;
  max-width: 25rem;
}

.secondRow2-cols {
  margin: 0;
}

.secondRow2-cols-input {
  width: 100%;
}

.secondRow-form {
  display: flex;
  justify-content: center;
  align-items: center;
}

.connecter > h3 {
  padding-bottom: 2.5rem;
  color: rgb(255, 255, 255);
}

/*------------INPUT-ROOT-CSS------------*/
.MuiInputBase-input {
  width: 100% !important;
}

.MuiFormLabel-root {
  color: rgb(255, 255, 255) !important;
}

.MuiInput-underline:after {
  border-bottom: 2px solid rgb(255, 255, 255) !important;
}

.MuiInput-underline:before {
  border-bottom: 1px solid rgb(255, 255, 255) !important;
}

.PhoneInputInput {
  font: inherit !important;
  color: currentColor !important;
  width: 100% !important;
  border: 0 !important;
  height: 1.1876em !important;
  margin: 0 !important;
  display: block !important;
  padding: 2rem 0 7px !important;
  min-width: 0 !important;
  background: none !important;
  box-sizing: content-box !important;
  animation-name: mui-auto-fill-cancel !important;
  letter-spacing: inherit !important;
  animation-duration: 10ms !important;
  -webkit-tap-highlight-color: transparent !important;
  border-bottom: 2px solid rgb(255, 255, 255) !important;
}

#standard-basic {
  color: white !important;
}

.contact-us-mainRow {
  padding: 4rem;
}

.contact-us-leftSide-inner-col1 {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background-color: #d2ac2c;
}

.contact-us-leftSide-inner-col2 {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgb(211, 176, 63);
  font-family: "Montserrat", sans-serif;
  padding: 1rem 0;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  text-align: center;
}

.secondRow2-col {
  padding: 0 !important;
  background-color: rgba(51, 62, 81, 0.562);
  display: flex;
  justify-content: center;
  align-items: center;
}

.contact-us-us-word {
  color: #333e51;
}

.h3-say-hello {
  padding: 1rem 0;
}

.contatct-us-leftSide-para {
  max-width: 20rem;
  padding-bottom: 1rem;
}

.secondRow2-cols-submit-button {
  display: flex;
  justify-content: center;
  align-items: center;
}

/*----------------------------------------*/
/*-----------------------------MEDIA QUERY-------------------*/
@media screen and (max-width: 1200px) {
  .contact {
    display: block;
  }
  .contact-us-floating-application-form {
    transform: scale(0.7);
  }
  .secondRow2 {
    width: fit-content;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 1rem;
    padding: 1rem 2rem;
    margin: 0 !important;
    border-left: 15px dashed rgb(210, 172, 44);
  }

  .secondRow2-title {
    font-size: 1.5rem;
    padding-bottom: 0rem;
    padding-top: 2rem;
    max-width: 15rem;
  }

  .secondRow2-cols {
    padding: 0;
    margin: 0;
  }

  .secondRow2-form-button {
    padding: 0.5rem 8rem !important;
  }

  .contact-us-mainRow {
    padding: 0;
  }

  .contatct-us-leftSide-para {
    font-size: 0.8rem;
    max-width: 15rem;
  }

  .contact-us-leftSide-inner-col1 {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background-color: transparent;
    color: black;
    margin-bottom: 3rem;
  }

  .contact-us-mainRow {
    margin-bottom: 3rem;
  }

  .contact-us-leftSide-inner-col2 {
    background-color: rgb(210, 172, 44);
  }

  .contact-us-vertical-word {
    padding-left: 0.7rem;
  }
}
