@import url("https://fonts.googleapis.com/css?family=Montserrat:400,700");
@import url("https://fonts.googleapis.com/css2?family=Ubuntu&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Source+Sans+Pro&display=swap");

/*-------------------------------------- NAVBAR SECTION beganning------------------------------------------------*/
button {
  outline: none !important;
}
.navBar {
  width: 100%;
  position: fixed;
  font-family: "Montserrat", sans-serif;
  z-index: 22 !important;
  transition: 1s;
}

.royalBlue {
  background-color: #36455f !important;
}
.nav-link {
  margin: 0 !important;
  font-size: 1rem;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0 1.1rem !important;
  font-weight: 500;
  transition: 1.5s;
}

.navbar-brand {
  margin: 0 0rem !important;
  padding: 0rem 1.5rem;
  /* box-shadow: rgba(0, 0, 0, 0.35) 0px 4px 10px;
    border-radius: 100%;*/
}

.navBarBrand-img {
  margin: 0 !important;
  opacity: 1;
}

.nav-link:hover {
  color: rgba(255, 255, 255, 0.925);
}

.navbar-toggler {
  margin-top: 0.2rem !important;
  border-radius: 100%;
  padding: 0.3rem 0.3rem !important;
  line-height: 0.8 !important;
}

.navbar-light .navbar-toggler-icon {
  outline: none;
  background-image: url("data:image/svg+xml,<svg viewBox='0 0 30 30' xmlns='http://www.w3.org/2000/svg'><path stroke='rgba(255,255,255, 0.8)' stroke-width='2' stroke-linecap='round' stroke-miterlimit='10' d='M4 7h22M4 15h22M4 23h22'/></svg>") !important;
  width: 1.4rem !important;
  height: 1.4rem !important;
}

.navBarBrand {
  opacity: 0.9;
}

.navBarBrand2 {
  display: none;
  padding: 0;
}

.aboutLink,
.servicesLink,
.contactLink {
  text-decoration: none !important;
  color: rgb(255, 255, 255) !important;
}

.nav-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  text-align: center;
}

.navItems {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  transition: 1s;
}

.navbar-items-div {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  text-align: center;
  flex-direction: row;
}

@media screen and (max-width: 1200px) {
  .responsive-navbar-nav {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    text-align: left;
    flex-direction: column;
  }

  .nav-container {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    text-align: left;
  }

  .navbar-items-div {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }

  .navbar-bootstrapNavbar {
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    flex-direction: column;
    text-align: right;
  }

  .nav-link {
    margin: 0 !important;
    color: rgba(255, 255, 255, 0.925);
    font-size: 0.9rem;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0.8rem 0rem !important;
    font-weight: 500;
  }

  .navItems {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
  }

  .navBarBrand-img {
    margin-top: 0.25rem;
    opacity: 1;
  }
}

/*-------------NAVBAR ROOT CSS----------------*/
