@import url("https://fonts.googleapis.com/css?family=Montserrat:400,700");

.services-mini {
  font-family: "Montserrat", sans-serif;
  padding: 3.1rem 0;
}
.services-mini-header-section-row {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-align: center;
}

.services-mini-header-section-h6-subtitle-col {
  padding-top: 2rem;
}
.services-mini-header-h6-subtitle {
  color: rgba(0, 0, 0, 0.767);
  letter-spacing: 0.2rem;
  font-weight: 500;
}
.services-mini-header-section-h1-title-col {
  padding-bottom: 0.5rem;
}
.services-mini-header-h1-title {
  color: black;
  font-weight: 600;
  font-size: 3.2rem;
}
.services-mini-header-section-p-text-col {
  padding-bottom: 1.2rem;
  display: flex;
  justify-content: center;
  align-items: center;
}
.services-mini-header-p-text {
  max-width: 60rem;
  font-size: 0.9rem;
}
.services-mini-header-section-button-col {
  padding-bottom: 3rem;
}
.services-mini-header-button {
  border-radius: 99999px !important;
  color: white !important;
  padding: 0.3rem 2rem !important;
  background-color: #333d51 !important;
}
.services-mini-body-content-row {
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  text-align: left;
  background: rgb(255, 255, 255);
  background: linear-gradient(
    0deg,
    rgba(255, 255, 255, 1) 20%,
    rgba(51, 62, 81, 0.95) 20%,
    rgba(51, 61, 81, 0.95) 70%,
    rgba(255, 255, 255, 1) 70%
  );
}
.services-mini-leftSide-cards {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin-top: 5rem;
}
.services-mini-rightSide-cards {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin-bottom: 5rem;
}
.services-mini-leftSide-upperCard,
.services-mini-leftSide-lowerCard,
.services-mini-rightSide-upperCard,
.services-mini-rightSide-lowerCard {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: column;
  text-align: left;
  box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px,
    rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;
  margin: 1rem 0;
  background-color: #f4f3ea;
  border-radius: 4%;
}

.services-mini-card-icon {
  color: #d2ac2c !important;
  display: flex !important;
  justify-content: center !important;
  align-items: center !important;
  margin: 0.5rem 0rem !important;
}
.services-mini-card-h2-subtitle {
  font-size: 1.5rem;
  background-color: #d2ac2c;
  color: black;
  margin: 0.5rem;
  font-weight: 600;
}
.services-mini-card-p-text {
  font-size: 1rem;
  max-width: 25rem;
  padding: 0.8rem 1rem;
  color: black;
}
.a-attribute {
  color: black !important;
  text-decoration: none !important;
}
