@import url("https://fonts.googleapis.com/css?family=Montserrat:400,700");
@import url("https://fonts.googleapis.com/css2?family=Nunito&family=Roboto&family=Ubuntu&display=swap");
@import url('https://fonts.googleapis.com/css2?family=Domine:wght@600&display=swap');

.services {
  font-family: "Montserrat", sans-serif;
}


.dev-icon {
  color: rgb(0, 0, 0) !important;
  font-size: 3.5rem !important;
  margin-bottom: 0.5rem;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: 0.3s ease-in;
}


.services-card:hover {
  background-color: #CAD0D7;
  transform: scale(1.1);
  z-index: 1;
  color: black;
}


.cardTitle:hover {
  -webkit-animation: tracking-in-expand 1s cubic-bezier(0.215, 0.610, 0.355, 1.000) both;
  animation: tracking-in-expand 1s cubic-bezier(0.215, 0.610, 0.355, 1.000) both;
}


@-webkit-keyframes tracking-in-expand {
  0% {
    letter-spacing: -0.5em;
    opacity: 0;
  }

  40% {
    opacity: 0.6;
  }

  100% {
    opacity: 1;
  }
}

@keyframes tracking-in-expand {
  0% {
    letter-spacing: -0.5em;
    opacity: 0;
  }

  40% {
    opacity: 0.6;
  }

  100% {
    opacity: 1;
  }
}


.services {
  padding-top: 0rem;
}

.services-image-background {
  background: url(https://i.ibb.co/6Dc0Hhp/services-left-Side-image.jpg) no-repeat left;
  background-size: cover;
  width: 100%;
  height: 100%;
}

.services-title-col-container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background-color: #333E51;
  background-image: linear-gradient(315deg, #333E51 0%, #333E51 74%);
  padding: 3rem 0;
}

.services-h1-title {
  font-weight: 600;
  color: rgb(255, 255, 255);
  text-align: center;
  font-size: 2.5rem;
  letter-spacing: 0.3rem;
  margin: 0.5rem 0;
  font-family: "Montserrat",
  sans-serif;
}



.services-col-rightSide-container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background-color: #333E51;
  background-image: linear-gradient(315deg, #333E51 0%, #333E51 74%);
}

.services-col-leftSide-container {
  padding: 0 !important;
}

.services-service-box {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: row !important;
}

.services-card {
  padding: 1rem 5rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex: none !important;
}

.cardTitle {
  color: white;
}

.services-service-box-button {
  padding: 3rem 0;
  display: flex;
  justify-content: center;
  align-items: center;
  flex: none !important;
}

.services-services-button-us {
  color: rgb(255, 255, 255) !important;
  border-radius: 999999px !important;
  background-color: #333e51 !important;
  padding: 0.4rem 2.3rem !important;
}

@media screen and (max-width: 1100px) {
  .services-h1-title {
    font-size: 2.1rem;
  }

  .services-card {
    padding: 3rem 1.5rem;
    flex: 0 0 0%;
  }

  .services-service-box-button {
    padding: 3rem 0;
   flex: none !important;
  }

  .dev-icon {
    font-size: 2.3rem !important;
  }

  .cardTitle {
    font-size: 1.2rem !important;
  }

  .services-card:hover {
    transform: scale(1);

  }

}

*::-webkit-media-controls-panel {
  display: none !important;
  -webkit-appearance: none;
}

/* Old shadow dom for play button */

*::-webkit-media-controls-play-button {
  display: none !important;
  -webkit-appearance: none;
}

/* New shadow dom for play button */

/* This one works! */

*::-webkit-media-controls-start-playback-button {
  display: none !important;
  -webkit-appearance: none;
}