@import url("https://fonts.googleapis.com/css?family=Montserrat:400,700");
@import url("https://fonts.googleapis.com/css2?family=Domine:wght@600&display=swap");

.blog {
  font-family: "Montserrat", sans-serif;
}

.blog-allTheNew-row {
  background: linear-gradient(
    to top,
    rgba(210, 171, 44, 0.938) 50%,
    #ffffff 30%
  );
  padding: 2rem 0;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}
.blog-allTheNew-4 {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.blog-allTheNew-title-col {
  margin-bottom: 1rem;
}

.blog-allTheNew-h1-title {
  font-size: 3rem;
  font-family: "Montserrat", sans-serif;
  letter-spacing: 0.2rem;
  font-weight: 600;
}

.blog-allTheNew-h1-title-span {
  color: #d2ac2c;
}

.blog-allTheNew-itemCard {
  display: flex;
  justify-content: center;
  align-items: center;
}

.blog-allTheNew-image {
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  border-radius: 1%;
  position: relative;
  margin-top: 1rem;
}

.blog-overPhohto-content-div {
  color: white;
  background: linear-gradient(to top, #363e50da 0%, #ffffff00 100%);
  padding: 0rem 0;
  padding: 2rem 0;
  position: absolute;
  bottom: 0rem;
  width: 400px;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  flex-direction: column;
  height: 100%;
}

.blog-overPhohto-content-para {
  max-width: 20rem;
  text-align: center;
  padding: 0 1rem;
  transition: ease-in-out;
}

.blog-overPhohto-content-buttonAndIcon-div {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.blog-overPhohto-content-icon:hover {
  color: #d2ac2c;
  cursor: pointer;
}

.blog-overPhohto-content-button {
  background-color: #363e50 !important;
  color: white !important;
  border-radius: 0px !important;
  padding: 0.2rem 1.2rem !important;
  border: none !important;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}

.blog-recentPosts-main-row {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgb(243, 243, 243);
  padding-bottom: 2rem;
}

.blog-recentPosts-h1-title {
  font-size: 2.8rem;
  font-family: "Montserrat", sans-serif;
  letter-spacing: 0.2rem;
  padding: 3rem 2rem;
  border-bottom: 2px solid #d2ac2c;
  font-weight: 600;
}

.blog-recentPosts-col {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin: 2rem;
  box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px;
}

.blog-recentPosts-upperimage {
  box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px;
}

.blog-recenetPosts-text-content-div {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  max-width: 370px;
  padding: 1rem 0;
}

.blog-recentPosts-subtitle-div {
  width: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: row;
}

.hashtags-div {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: row;
}

.blog-recenetPosts-hashtag-span1,
.blog-recenetPosts-hashtag-span2,
.blog-recenetPosts-hashtag-span3,
.blog-recenetPosts-hashtag-span4,
.blog-recenetPosts-hashtag-span5,
.blog-recenetPosts-hashtag-span6,
.blog-recenetPosts-hashtag-span7,
.blog-recenetPosts-hashtag-span8,
.blog-recenetPosts-hashtag-span9,
.blog-recenetPosts-hashtag-span10,
.blog-recenetPosts-hashtag-span11,
.blog-recenetPosts-hashtag-span12 {
  margin: 0.5rem 0.5rem;
  color: white;
  border-radius: 99999px;
  padding: 0.2rem 0.8rem;
}

.blog-recenetPosts-hashtag-span1 {
  background-color: tomato;
}

.blog-recenetPosts-hashtag-span2 {
  background-color: rgb(71, 114, 255);
}

.blog-recenetPosts-hashtag-span3 {
  background-color: rgb(255, 71, 184);
}

.blog-recenetPosts-hashtag-span4 {
  background-color: rgb(180, 167, 50);
}

.blog-recenetPosts-hashtag-span5 {
  background-color: rgb(224, 71, 255);
}

.blog-recenetPosts-hashtag-span6 {
  background-color: rgb(71, 218, 255);
}

.blog-recenetPosts-hashtag-span7 {
  background-color: rgb(223, 45, 45);
}

.blog-recenetPosts-hashtag-span8 {
  background-color: rgb(71, 255, 215);
}

.blog-recenetPosts-hashtag-span9 {
  background-color: rgb(255, 169, 71);
}

.blog-recenetPosts-hashtag-span10 {
  background-color: rgb(255, 71, 80);
}

.blog-recenetPosts-hashtag-span11 {
  background-color: rgb(71, 255, 117);
}

.blog-recenetPosts-hashtag-span12 {
  background-color: rgb(109, 100, 99);
}

.blog-recentPosts-profilePic-image {
  margin: 0.5rem 0;
  margin-right: 1rem;
}
@media screen and (max-width: 1100px) {
  .blog-recenetPosts-text-content-div {
    padding: 1.5rem;
  }
}
