@import url("https://fonts.googleapis.com/css?family=Montserrat:400,700");
@import url("https://fonts.googleapis.com/css2?family=Nunito&family=Roboto&family=Ubuntu&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@300;400&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Courier+Prime&family=IBM+Plex+Sans:wght@300&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Source+Sans+Pro&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Domine:wght@600&display=swap");

.join-us {
  font-family: "Montserrat", sans-serif;
  color: rgb(0, 0, 0);
  margin-bottom: 5rem;
}

.join-us-mainRow {
  background: linear-gradient(to top, #bb9723 50%, #ffffff 50%);
  padding: 1rem 0rem;
}

.join-us-page-title {
  display: flex;
  align-items: center;
  justify-content: center;
}

.join-our-team-h1-title {
  font-size: 3rem;
  padding-bottom: 3rem;
  padding-top: 1rem;
  font-family: "Montserrat", sans-serif;
}

.join-our-team-span,
.itpeaks-email-span {
  color: #d2ac2c;
}

.job-on-board-card {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #333e51;
  color: white;
  margin: 1rem 0;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  padding: 3rem;
}

.job-on-board-card-h1-title {
  padding: 1rem;
}

.job-on-board-card-para-content {
  display: flex;
  justify-content: center;
  align-items: center;
}

.job-vacancies-card {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  color: white;
}

.job-vacancies-rightSide {
  padding: 1rem 2rem;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.apply-button {
  color: rgb(255, 255, 255) !important;
  border-radius: 999999px !important;
  background-color: #d2ac2c !important;
  padding: 0.1rem 1rem !important;
  text-decoration: none !important;
}

.job-vacancies-leftSide-col {
  display: flex;
  justify-content: center;
  align-items: center;
}

.join-us-main-secondRow2 {
  display: flex;
  justify-content: center;
  align-items: center;
  background: rgb(255, 255, 255);
  background: linear-gradient(
    90deg,
    rgba(255, 255, 255, 1) 73%,
    rgba(210, 172, 44, 1) 73%
  );
}

.secondRow2-1-cols-button,
.secondRow2-1-cols-textarea {
  padding: 1rem 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

.join-us-phone-number-input {
  color: white !important;
}

.inputfile {
  cursor: pointer;
  opacity: 0;
  overflow: hidden;
  position: absolute;
  z-index: -1;
}

.inputfile + label {
  padding: 0rem;
  color: rgb(0, 0, 0);
  background-color: transparent;
}

.inputfile + label {
  cursor: pointer;
}

.file-input-div {
  padding: 2rem 0rem;
  margin-top: 2rem;
  border-radius: 999999px;
  background-color: #ffffff;
  text-align: center;
}

.join-us-leftSide-inner-col2 {
  color: rgb(0, 0, 0);
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  font-family: "Montserrat", sans-serif;
  text-align: left;
}

.join-us-secondRow2-col {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  text-align: left;
}

.join-us-leftSide-inner-col2-h1 {
  font-size: 3.5rem;
  font-weight: 600;
  letter-spacing: 0.2rem;
}
.join-us-rightSide-main-col {
  background-color: rgb(17, 17, 17);
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.job-vacancies-rightSide-col {
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
}
.job-vacancies-buttons-div {
  padding: 0.5rem 0 !important;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.join-us-job-vacancies-cards-row {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 3rem 0;
  width: 100%;
}
.join-us-job-vacancies-card-title-div {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: column;
  text-align: left;
  width: 100%;
}

.secondRow2-cols-1 {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
}

.secondRow2-1-cols {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
}
.join-us-main-secondRow2-col {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.join-us-form-button {
  margin: 2rem 0 !important;
  background-color: rgba(51, 63, 81, 1) !important;
  color: white !important;
  padding: 0.5rem 2rem !important;
}
@media screen and (max-width: 1200px) {
  .join-us-mainRow {
    background: linear-gradient(to top, #bb9723 50%, #ffffff 50%);
    padding: 0rem;
  }

  .join-our-team-h1-title {
    font-size: 2.1rem;
    padding-bottom: 1rem;
    padding-top: 1rem;
    font-family: "Montserrat", sans-serif;
  }

  .job-on-board-card {
    color: white;
    margin: 0rem;
    box-shadow: none;
    padding: 2rem;
  }

  .job-on-board-card-h1-title {
    font-size: 1.5rem;
    text-align: center;
  }

  .jobOnBoard-subtitle-text {
    font-size: 0.8rem;
    max-width: 20rem;
  }

  .job-vacancies-rightSide {
    padding: 0rem;
  }

  .thirdRow4-cols2-h4 {
    font-size: 1.1rem;
    padding: 1rem 0;
  }

  .thirdRow4-cols2-h6 {
    font-size: 0.8rem;
  }

  .job-vacacies-card-para-text {
    padding-top: 1rem;
    font-size: 0.8rem;
    max-width: 14rem;
  }

  .job-vacancies-buttons-div {
    transform: scale(0.75);
    padding-bottom: 1rem;
  }

  .job-vacancies-leftSide-images {
    width: 200%;
    height: 100%;
    padding: 1rem 0;
  }

  .join-us-main-secondRow2 {
    margin: 3rem 0 !important;
  }

  .join-us-secondRow2-title {
    font-size: 1.5rem;
    color: rgb(255, 255, 255);
    font-family: "Montserrat", sans-serif;
    padding-bottom: 1rem;
    letter-spacing: 0.1rem;
  }
}
