@import url("https://fonts.googleapis.com/css?family=Montserrat:400,700");
@import url("https://fonts.googleapis.com/css2?family=Julius+Sans+One&family=Megrim&family=Parisienne&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Caesar+Dressing&display=swap");
.footer {
  background-color: #ffffff;
  font-family: "Montserrat", sans-serif;
  color: rgb(0, 0, 0);
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  width: 100%;
}

.socialMedia-div {
  text-align: center;
  display: flex;
  align-items: center;
  justify-self: center;
  flex-direction: row;
}

.mdb-links {
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
}

.MuiSvgIcon-root {
  color: #ecc852 !important;
}

.a-copyrights {
  color: rgb(93, 124, 172) !important;
  text-decoration: none !important;
}

.footer-firstRow {
  display: flex;
  align-items: flex-start;
  justify-content: space-evenly !important;
  width: 100%;
  padding: 1.5rem 2rem;
}

.footer-secondRow {
  border-top: 1px solid #84addb;
  padding: 0.5rem 0;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 80%;
}

.footer-firstRow-col1,
.footer-firstRow-col2 {
  display: flex;
  align-items: center;
  justify-content: center;
}

.footer-firstRow-col2 {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: column;
  margin: 2rem 0;
}

.footer-subscribe-header {
  font-size: 0.9rem;
}

.footer-subscribe-subtitle {
  font-size: 0.8rem;
}

.footer-firstRow-innerRow {
  display: flex;
  align-items: flex-start;
  justify-content: center;
  padding: 1rem 0rem;
}

.f-f-cols-1 {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  flex-direction: column;
}

.footer-topLink {
  font-size: 1rem;
  font-weight: 600;
}

.footer-topLink-underline {
  width: 90%;
  padding: 0.1rem 0;
  background-color: #cad0d7;
}

.f-f-cols-1-links-div {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  flex-direction: column;
  padding: 0.8rem 0;
  font-size: 0.85rem;
  color: rgb(0, 0, 0);
}

.f-f-cols-1-links-div > a {
  padding: 0.2rem 0;
}

.footer-Link,
.footer-Link:hover {
  color: rgb(0, 0, 0);
  text-decoration: none !important;
}

.footer-Link:hover {
  color: rgb(36, 36, 36);
}

.footer-firstRow-col2-row1 {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  flex-direction: column;
  padding: 1rem 0;
  text-align: left;
}

.footer-firstRow-col2-1 {
  display: contents;
}

.footer-input {
  width: 60%;
}

.footer-firstRow-col2-row2 {
  display: flex !important;
  justify-content: flex-start !important;
  align-items: flex-end !important;
  padding: 1rem 0;
}

.socialMedia-div {
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

.icon-inner-div {
  padding: 0.4rem 0.5rem;
  margin-top: 1rem;
  background-color: rgb(51, 63, 81);
  margin-right: 0.4rem;
  display: flex !important;
  align-items: center !important;
  justify-content: center !important;
  text-align: center !important;
  width: 100%;
}

.page-footer {
  padding: 0 !important;
  margin: 0 !important;
}
.footerLogo {
  font-family: "Caesar Dressing", cursive;
  font-size: 2.5rem;
  margin-top: 0 !important;
}
.footer-first-from-leftSide {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
}
.footer-first-from-leftSide > h6 {
  font-size: 0.9rem;
  font-weight: 600;
  max-width: 15rem;
}
@media screen and (max-width: 1200px) {
  .footer {
    padding-top: 0rem;
    font-size: 0.8rem;
  }

  .footer-topLink {
    font-size: 0.6rem;
    padding: 0;
    margin: 0;
  }

  .f-f-cols-1-links-div {
    text-align: left;
    font-size: 0.5rem;
    padding: 0;
    margin: 0;
  }

  .footer-subscribe-header {
    font-size: 0.6rem;
    padding: 0;
    margin: 0;
  }

  .footer-subscribe-subtitle {
    font-size: 0.5rem;
    padding: 0;
    margin: 0;
  }

  .footer-firstRow-col2 {
    padding: 0;
    margin: 0;
  }

  .footer-firstRow-col2-row2,
  .footer-firstRow-col2-2 {
    padding: 0;
    margin: 0;
  }

  .footer-input,
  .footer-firstRow-col2-1,
  .footer-button {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0;
    margin: 0;
    width: 70%;
  }

  .footer-firstRow-col2-row3 {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0;
    margin: 0;
  }

  .footer-firstRow-col2-3,
  .socialMedia-div,
  .icon-inner-div,
  .icon-link,
  .icon-inner-div {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0;
    margin: 0.1rem 0;
    font-size: 0.5rem;
    border: none;
  }

  .footer-copyright {
    font-size: 0.5rem;
  }

  .itpeaks-brand-name {
    font-size: 1rem;
  }

  .footer-topLink-underline {
    width: 90%;
    padding: 0.1rem 0;
    background-color: #cad0d7;
    margin: 0.4rem 0;
  }
}
